<template>
	<view>
		<u-tabbar
			:value="value6"
			@change="name => toThisPage(name)"
			:fixed="true"
			:placeholder="true"
			:safeAreaInsetBottom="true"
		>
			<u-tabbar-item 
			v-for="(item,index) in tabBarData"
			:key="index"
			:text="item.text" :icon="item.icon"
			></u-tabbar-item>
		</u-tabbar>
	</view>
</template>

<script>
	export default {
		name:"tabBar",
		data() {
			return {
				value6:0,
				tabBarData: [{
					text:"首页",
					icon:"home"
				},
				{
					text:"放映厅",
					icon:"photo"
				},
				{
					text:"消息",
					icon:"play-right"
				},
				{
					text:"我的",
					icon:"account"
				}]
			};
		},
		methods: {
			toThisPage(e){
				this.value6 = e 
				if (e == 0) {
					uni.redirectTo({
						url:"/pages/index/index"
					})
				}
				else if(e == 1){
					uni.redirectTo({
						url:"/pages/bbs/bbs"
					})
				}
				else if(e == 2){
					uni.redirectTo({
						url:"/pages/message/message"
					})
				}
				else{
					uni.redirectTo({
						url:"/pages/user/user"
					})
				}
			}
		}
	}
</script>

